const SELECTOR_GALLERY_ITEMS = '[data-gallery-items]';
const SELECTOR_GALLERY_BTN = '[data-gallery-btn]';

const loadMoreBtn =
  document.querySelectorAll<HTMLElement>(SELECTOR_GALLERY_BTN);

// Add class visible to item in gallery
loadMoreBtn.forEach((btn) => {
  btn.addEventListener('click', () => {
    const galleryItems = document.querySelectorAll<HTMLElement>(
      SELECTOR_GALLERY_ITEMS
    );

    if (!galleryItems) return;

    galleryItems.forEach((item) => {
      item.classList.remove('is-invisible');
    });

    if (galleryItems.length > 4) {
      btn.classList.add('is-disabled');
    }
  });
});
